import React from 'react'
import './emailform.css'

import emailjs from 'emailjs-com';



//IMPORTING EMAIL SVG
import {ReactComponent as EmailSVG} from '../../../assets/emial.svg'

function emailform() {

    function sendEmail(e) {
        e.preventDefault();
    
        emailjs.sendForm('service_bnw6u9q', 'template_uydi624', e.target, 'user_bIRClhDHYUKAokqlSVR5M')
          .then((result) => {
              console.log(result.text);
          }, (error) => {
              console.log(error.text);
          });
      }

    return (
        <div className='email-form-wrapper'>
      <div className='email-form'>
      <form className="contact-form" onSubmit={sendEmail}>
      <input  type="hidden" name="contact_number" />
      <div className='email-name-ctn'>
      <label className='name-lable'>Name</label>
      <input id='name-field' placeholder='Name' className='user_name_field' type="text" name="user_name" />
      </div>
      <div className='email-email-ctn'>
      <label className='email-name'>Email</label>
      <input id='email-field' placeholder='email' className='user_email_field' type="email" name="user_email" />
      </div>
      <div className='email-message-ctn'>
      <label className='message-lable'>Message</label>
      <textarea id='text-field' className='user_message_field' placeholder='write your message' name="message" />
      </div>
      <input className='send-email-button' type="submit" value="Send" />
    </form>
                <div className='email-form-right-side'>
                       
                       <EmailSVG className='email-svg'/>
                       
                </div>
            </div>
        </div>
    )
}

export default emailform
