import React from 'react'

import './seeprojectlink.css'

 import { Link, animateScroll as scroll } from "react-scroll";

function seeProject() {
    return (
        <div className='seeproject-link-ctn'>
                      <Link
     activeClass='active'
     to='skills-section'
     spy={true}
     smooth={true}
     offset={-1}
     duration={500}
     >
         <label className='projects-link'>Skills</label>
         </Link>
        </div>
    )
}

export default seeProject